.dailyScoreChart{
    background-color:#FBFBFB;
    height:100%;
    width: 100%;
    border-radius: 5px;
    grid-column: 3;
    grid-row: 2;
    position: relative;
}

.title-daily-score{
   position: absolute;
    top:15px;
    left: 15px;
    font-weight: bold;
}

.affichage-daily-score{
    position: absolute;
    top: 35%;
    z-index: 1;
    width: 100%;
    height: 75px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.score-pourcentage{
    margin-bottom: 10px;
    font-size: 26px;
    font-weight: bold;
}

.texte-score{
    text-align: center;
    font-size: 16px;
}

#recharts2-clip{
    background-color: red;
}

.progress-label{
    font-size: 20px;
    font-weight: bold;
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center;
    
}

.recharts-sector {
    stroke-linejoin: round;
    stroke-width: 7px;
    stroke-linecap: round;
    stroke-linejoin: round;
    border-radius: 10px;
  }

  .recharts-polar-angle-axis-tick-value {
    transform: translateY(0rem);
    margin-bottom: 30px;
    tspan{
        width: 50px;
        height: 50px;
    }
 
}

