.radarChartPerformance{
    background-color: #282D30;
    border-radius: 5px;
    grid-column: 2;
    grid-row: 2;
    width: 100%;
    height: 100%;
}

.recharts-polar-grid-angle{
    line{
        stroke: none;
    }
}


    .recharts-polar-angle-axis-tick-value {
        padding: 20px;
        tspan{
            font-size: 12px;
         
        }
            
    }
    


@media screen and (max-width:1439px){

    .recharts-polar-angle-axis-tick-value {
        tspan{
            font-size: 6px;
        }
            
    }
}